import { Component, ViewChild, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ProductService } from "../../providers/product.services";
import { environment } from "../../../environments/environment";
import { Clipboard } from '@ionic-native/clipboard/ngx';
import { ToastController, Platform } from '@ionic/angular';
import { UserData } from '../../providers/user-data';
import { SocialSharing } from '@ionic-native/social-sharing/ngx';

@Component({
  selector: 'page-wallet',
  templateUrl: 'wallet.html',
  styleUrls: ['./wallet.scss'],
})
export class WalletPage implements OnInit {

    public cashbackList: any=[]
    public searchWord:any;


    public noRecordsFound: any = true;

    constructor(
      private _ProductService: ProductService,
      private _Router: ActivatedRoute,
      private clipboard: Clipboard,
      public toastController: ToastController,
      private _UserData: UserData,
      private platform: Platform,
      private socialSharing: SocialSharing
    ) { }

    public isSearchEnable:Boolean=false;
  public baseUrl = environment.baseUrl + '/';
  public searchproductList:any=[];


  public referCode
     ngOnInit() {
      this.referCode = this._Router.snapshot.params.referCode;
      console.log(this.referCode, "referCode")
      this.getCashBackList();
      this.getCustomerProfile()

    }

    

    public showSearchBox: Boolean = false;
    enableSearch() {
      this.showSearchBox = true
    }

    backToNormal(){
      this.showSearchBox = false
    }

   public refershDefault = {
      target: {
        complete: function () {
          return true
        }
      }
    }


   async copy(code){
    console.log(code ,":: code ")
    this.clipboard.clear()
    this.clipboard.copy(code);
    const toast = await this.toastController.create({
      message: "refer code Copied",
      duration: 2000,
      position: "bottom",
      color:'success',
      animated: true,
    });
    toast.present();

    // this.toast.
  }

public userData={
  referCode:'',
  fullName:'',
  walletBalance:0,
  loyaltyCardNumber:''
}
  getCustomerProfile() {
    this._UserData
      .getCustomerDetails(localStorage.getItem("userId"))
      .subscribe((data: any) => {
        this.userData = data;
        //  ( this.userData.hasOwnProperty('fullName'))? '' : this.userData = "Guest"
        console.log(this.userData, "---");
      });
  }


    public total=0;

    getCashBackList(event = this.refershDefault) {
      let shopId = localStorage.getItem('shopID')
      this._ProductService.getWallet(localStorage.getItem('userId')).subscribe((data: any) => {
        console.log(data,"cashback list")
        this.cashbackList = data;
        this.cashbackList.forEach((list)=>{
            this.total = this.total + parseInt(list.amount)
        })
        if (this.cashbackList.length !== 0) {

          console.log(this.cashbackList, "cashbackList")
          this.noRecordsFound = false
        }else{
          this.noRecordsFound = true
        }


      if (event) { }
        setTimeout(() => {
          console.log('Async operation has ended');
          event.target.complete();
        }, 1500);
      });
    }

   async share(){
        await this.socialSharing
          .share("Use my Refer code : "+this.userData.referCode)
          .then(() => {})
          .catch((err) => {
            console.log(err);
          });
    }



  }
